.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  white-space: nowrap;
}

.btn {
  text-decoration: none;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: @greyish-brown;
    color: @white;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

img {
  max-width: 100%;
  height: auto;
}
